<template>
  <div id="creatSelec">
    <h1>请选择考试时间</h1>
    <p>CET考试时间定于每年6月和12月的第三个星期六</p>
    <h3>2017年</h3>
    <p @click="otherButtonShow">选择其他的考试时间</p>
    
    <div class="radio-lg">
      <ul>
        <li class="radioLi active" id="data-2016-6-17" @click="dateSelect('data-2016-6-17')">6月17日</li>
        <li class="radioLi noactive" id="data-2016-6-18" @click="dateSelect('data-2016-6-18')">6月18日</li>
        <div v-show="isShow">
           <li class="radioLi noactive" id="data-2016-6-19" @click="dateSelect('data-2016-6-19')">6月19日</li>
          <li class="radioLi noactive" id="data-2016-6-20" @click="dateSelect('data-2016-6-20')">6月20日</li>
           <li class="radioLi noactive" id="data-2016-6-21" @click="dateSelect('data-2016-6-21')">6月21日</li>
          <li class="radioLi noactive" id="data-2016-6-22" @click="dateSelect('data-2016-6-22')">6月22日</li>
        </div>
        

      </ul>
    </div>
    <bottomBtn value="下一步" color="orange" @click.native="toCreateSelec"></bottomBtn> 
  </div>
</template>

<script>

import bottomBtn from '../components/bottomBtn.vue'
export default {
  name: 'creatSelec',
  data(){
    return {
      isShow:false
    }
  },
  methods:{
    toCreateSelec:function(){
        this.$router.push({path:"/"})
    },
    dateSelect:function(id){
      var obj = document.getElementsByClassName("radioLi");
      for(var i=0;i<obj.length;i++){
          obj[i].setAttribute("class","radioLi noactive");
      }
      document.getElementById(id).setAttribute("class","radioLi active");
    },
    otherButtonShow:function(){
        this.isShow=true
    }
  },
  components:{bottomBtn}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import '../assets/css/variables.less';
#creatSelec{
  width:100%;
  height:2000px;
  background: pink
}
.active{
  background:#49AA83; 
}
.noactive{
  background:#F4F4F4;
}
</style>


