<template>
  <div id="index">
    <h1>{{ msg }}</h1>
    <button @click="toCreat">去create页面</button>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js index正式主页'
    }
  }, methods:{
    toCreat:function(){
        this.$router.push({path:"/Creat"})
    }
  }
 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../assets/css/variables.less';
#index{
  width:100%;
  height:2000px;
}
h1, h2 {
  width:100%;
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>


